// FIXME This bundle implicitly loads CodeMirror off of the window global. This should
// explicitly import the dependency to ensure it exists on the page. We usually include
// all editors in the same bundle as CodeMirror but this stack-template bundle is separate.
// Maybe dynamic imports solve this.

import type {Editor, MarkerRange, TextMarker} from 'codemirror'
import {render} from 'lit-html'
import type {TemplateResult} from 'lit-html'

declare const CodeMirror: {
  Pos: (line: number, ch: number) => {line: number; ch: number}
  on(...args: unknown[]): void
  Pass: unknown
}

export type CodeMirrorElement = Element & {
  CodeMirror: Editor
}

// Generic type compatible with CodeMirror
interface Position {
  line: number
  ch: number
}

export interface Validation {
  message?: TemplateResult
  severity: string
  fixes?: string[]
}

// This is the structure required by our custom linter
export type LintMessage = Validation & {
  from: Position
  to: Position
}

type Mark = TextMarker<MarkerRange> & {
  keepTooltip?: () => void
  destroyTooltip?: () => void
  __message: LintMessage
}

export interface TokenInfo {
  from: Position
  to: Position
}

export interface PathInfo {
  key: TokenInfo
  value: TokenInfo
}

export abstract class CodeMirrorLintingHelper {
  lintMarks: Mark[] = []
  lintTimeout: ReturnType<typeof setTimeout>

  abstract updateLinting(cm: Editor): void

  clearState() {
    for (const mark of this.lintMarks) {
      mark.clear()
      if (mark.destroyTooltip) mark.destroyTooltip()
    }
    this.lintMarks.splice(0)
  }

  createMarks(cm: Editor, messages: LintMessage[]) {
    for (const message of messages) {
      const {from, to, severity} = message
      const mark = cm.markText(from, to, {
        className: `CodeMirror-lint-mark CodeMirror-lint-mark-${severity}`,
        __message: message,
      } as Mark) as unknown as Mark
      this.lintMarks.push(mark)
    }
  }

  showTooltip(cm: Editor, event: Event) {
    const target = (event.target || event.srcElement)! as HTMLElement

    if (!target.classList.contains('CodeMirror-lint-mark')) return

    const box = target.getBoundingClientRect()
    const left = (box.left + box.right) / 2
    const top = (box.top + box.bottom) / 2
    const mark = findMark(cm, left, top)
    if (!mark) return

    const {__message: message} = mark
    if (!message) return

    const tooltip = this.createTooltip(message, box.left, top)
    const select = tooltip.querySelector<HTMLSelectElement>('select')!
    const button = tooltip.querySelector<HTMLElement>('button')!

    let timeout: ReturnType<typeof setTimeout> | null = null

    const destroyTooltip = () => {
      if (document.activeElement === select) return
      timeout = setTimeout(() => {
        delete mark.keepTooltip
        delete mark.destroyTooltip
        if (!tooltip.parentElement) return
        // animate it
        tooltip.style.opacity = '0'
        setTimeout(() => tooltip.parentElement && tooltip.parentElement.removeChild(tooltip), 600)
      }, 100)
    }

    const keepTooltip = () => clearTimeout(timeout!)

    CodeMirror.on(tooltip, 'mouseout', destroyTooltip)
    CodeMirror.on(target, 'mouseout', destroyTooltip)

    CodeMirror.on(tooltip, 'mouseover', keepTooltip)
    CodeMirror.on(target, 'mouseover', keepTooltip)

    mark.keepTooltip = keepTooltip
    mark.destroyTooltip = destroyTooltip

    const replaceValue = (value: string) => {
      const location = mark.find()!
      cm.focus()
      cm.setSelection(location.from, location.to)
      cm.replaceSelection(value)
      destroyTooltip()
    }

    button.addEventListener('click', () => replaceValue(button.textContent!))
    select.addEventListener('change', () => replaceValue(select.value))
    select.addEventListener('focusout', destroyTooltip)
  }

  createTooltip(lintMessage: LintMessage, left: number, top: number): HTMLElement {
    const {message, fixes} = lintMessage
    const tooltipTemplate = document.querySelector<HTMLTemplateElement>('#yaml-lint-template')!
    const tooltip = document.importNode(tooltipTemplate.content, true).querySelector<HTMLElement>('.js-yaml-lint')!
    const tooltipHeader = tooltip.querySelector<HTMLElement>('.js-yaml-lint-header')!
    const tooltipTitle = tooltip.querySelector<HTMLElement>('.js-yaml-lint-title')!
    const tooltipSubtitle = tooltip.querySelector<HTMLElement>('.js-yaml-lint-subtitle')!
    const tooltipMessage = tooltip.querySelector<HTMLElement>('.js-yaml-lint-message')!
    const tooltipFixes = tooltip.querySelector<HTMLElement>('.js-yaml-lint-fixes')!
    const fixButton = tooltipSubtitle.querySelector<HTMLElement>('button')!

    tooltipHeader.hidden = !fixes
    tooltipMessage.hidden = !!fixes
    tooltipSubtitle.hidden = !fixes
    tooltipFixes.hidden = !fixes || fixes.length < 2

    if (fixes) {
      render(message, tooltipTitle)
      const firstFix = fixes[0]!
      fixButton.textContent = firstFix
      const select = tooltipFixes.querySelector<HTMLElement>('select')!
      for (const fix of fixes.slice(1)) {
        const option = document.createElement('option')
        option.textContent = fix
        select.appendChild(option)
      }
    } else {
      render(message, tooltipMessage)
    }
    document.body.appendChild(tooltip)

    tooltip.classList.add('CodeMirror-lint-tooltip')
    tooltip.style.opacity = '1'
    tooltip.style.left = `${left}px`
    tooltip.style.top = `${top - tooltip.clientHeight - 18}px`
    return tooltip
  }
}

function findMark(cm: Editor, left: number, top: number) {
  const marks = cm.findMarksAt(cm.coordsChar({left, top}, 'window')) as unknown as Mark[]
  if (marks.length === 0) return null
  const mark = marks[0]!
  if (mark.keepTooltip) {
    mark.keepTooltip()
    return null
  }
  return mark
}

export function fileOffsetToLineAndColumn(doc: string, offset: number) {
  const lines = doc.split('\n')
  let startLine = 0
  let startColumn = 0
  let lineOffset = 0
  while (lineOffset + lines[startLine]!.length < offset) {
    lineOffset += lines[startLine]!.length + 1
    startLine++
  }
  startColumn = offset - lineOffset
  return {startLine, startColumn}
}
