import CodeMirror from 'codemirror'
// eslint-disable-next-line import/no-namespace
import * as jsonc from 'jsonc-parser'
import type {Editor} from 'codemirror'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {
  type CodeMirrorElement,
  CodeMirrorLintingHelper,
  fileOffsetToLineAndColumn,
  type LintMessage,
} from '../codemirror-linter-util'
import type {getJsonRootRuleSignature} from './json-rule-util-types'

import type {TemplateResult} from 'lit-html'
import {html} from 'lit-html'

class JsonCodeMirrorLintingHelper extends CodeMirrorLintingHelper {
  getRootRule: getJsonRootRuleSignature
  constructor(_getRootRule: getJsonRootRuleSignature) {
    super()
    this.getRootRule = _getRootRule
  }

  async updateLinting(cm: Editor) {
    const annotations = lint(cm.getValue(), this.getRootRule)
    cm.operation(() => {
      this.clearState()
      this.createMarks(cm, annotations)
    })
  }
}

export function lint(text: string, _getRootRule: getJsonRootRuleSignature): LintMessage[] {
  const jsonErrors = getJsoncErrors(text)
  const messages: LintMessage[] = []

  for (const error of jsonErrors) {
    const {startLine, startColumn} = fileOffsetToLineAndColumn(text, error.offset)
    const from = {line: startLine, ch: startColumn}
    const to = {line: startLine, ch: startColumn + error.length}
    messages.push({
      from,
      to,
      message: tooltipMessage(`JSONC Parser: ${jsonc.printParseErrorCode(error.error)}`),
      severity: 'error',
    })
  }
  return messages
}

function tooltipMessage(value: string): TemplateResult {
  return html` <p>${value}</p> `
}

export function registerLinterEvents(getRootRule: getJsonRootRuleSignature) {
  const codeMirrorLintingHelper = new JsonCodeMirrorLintingHelper(getRootRule)
  observe('.CodeMirror', {
    add(codeMirror: Element) {
      const anyEditor = codeMirror as CodeMirrorElement
      const editor = anyEditor.CodeMirror

      editor?.on('change', () => {
        clearTimeout(codeMirrorLintingHelper.lintTimeout)
        codeMirrorLintingHelper.lintTimeout = setTimeout(() => codeMirrorLintingHelper.updateLinting(editor), 500)
      })
      CodeMirror?.on(editor.getWrapperElement(), 'mouseover', (event: Event) =>
        codeMirrorLintingHelper.showTooltip(editor, event),
      )
      codeMirrorLintingHelper.updateLinting(editor)
    },
  })
}

function getJsoncErrors(doc: string) {
  // JSONC parsing
  const errors: jsonc.ParseError[] = []
  jsonc.parse(doc, errors)
  return errors
}
