import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

function listOfFeatures() {
  return document.querySelector<HTMLElement>('#js-devcontainer-editor-sidebar-content-complete')!
}

function featureDetails() {
  return document.querySelector<HTMLElement>('#js-devcontainer-editor-sidebar-details')!
}

function doFilter(searchTerm: string) {
  const searchTermButSmaller = searchTerm.toLowerCase()
  const allFeatures = listOfFeatures().querySelectorAll<HTMLElement>('.js-devcontainer-editor-sidebar-feature')
  for (const feature of allFeatures) {
    const featureName = feature.getAttribute('devcontainer-editor-sidebar-feature-search-tag')!
    const featureMatches = featureName.toLowerCase().includes(searchTermButSmaller)
    feature.hidden = !featureMatches
  }
}

function showAllFeatures() {
  const allFeatures = listOfFeatures().querySelectorAll<HTMLElement>('.js-devcontainer-editor-sidebar-feature')
  for (const feature of allFeatures) {
    feature.hidden = false
  }
}

document.addEventListener('DOMContentLoaded', () => {
  showAllFeatures()
})

// client side search so onInput should be safe
// eslint-disable-next-line delegated-events/no-high-freq
on('input', '#dev-container-search-input', async function (event) {
  const target = event.target as HTMLInputElement
  const searchValue = target.value

  if (searchValue.length === 0) {
    showAllFeatures()
    return
  }
  doFilter(searchValue)
})

// More details on an individual Feature
on('click', '[devcontainer-editor-sidebar-url]', async function (event) {
  const inputElement = document.querySelector<HTMLInputElement>('#dev-container-search-input')!
  const link = event.currentTarget
  const url = link.getAttribute('devcontainer-editor-sidebar-url')!

  listOfFeatures().hidden = true
  inputElement.hidden = true

  const html = await fetchSafeDocumentFragment(document, url)
  featureDetails().append(html)
})

// "Go Back"
on('click', '[devcontainer-editor-sidebar-search-url]', function () {
  const inputElement = document.querySelector<HTMLInputElement>('#dev-container-search-input')!
  showAllFeatures()
  inputElement.value = ''
  featureDetails().textContent = ''
  listOfFeatures().hidden = false
  inputElement.hidden = false
})

on('remote-input-success', '.js-remoteinput', ({currentTarget}) => {
  if (currentTarget instanceof HTMLElement) currentTarget.hidden = false
})
